<template>
  <el-container>
    <el-main class="hidden-sm-and-down">
      <!-- <lottie :options="animation.options[1]" :height="750" :width="750" v-on:animCreated="handleAnimation"/> -->
      <lottie :options="animation.options[2]" :height="200" :width="550" v-on:animCreated="handleAnimation"/>
    </el-main>
    <el-aside>

      <div class="login-container">
        <h2>LOGIN</h2>
        <el-row>
          <!-- <el-col :span="12"><img style="width:150px;height:150px" src="./../../assets/droidtocat.png" alt="Logo"></el-col> -->
          <el-col :span="24"><lottie :options="animation.options[0]" :height="180" :width="180" v-on:animCreated="handleAnimation"/></el-col>
        </el-row>

        <el-form :model="loginForm" :rules="loginRules" ref="loginForm" class="demo-loginForm">
          <el-form-item prop="username">
            <el-input type="text" v-model="loginForm.username" placeholder="username"></el-input>
          </el-form-item>

          <el-form-item prop="password">
            <el-input type="password" v-model="loginForm.password" @keyup.enter.native="handleLogin" placeholder="password"></el-input>
          </el-form-item>

          <el-button type="primary" @click="handleLogin" :loading="loading" class="signin-btn">Sign In</el-button>
        </el-form>
      </div>

    </el-aside>
  </el-container>
</template>

<script>
import Lottie from "vue-lottie";
import * as anim1 from "../../assets/lottie/react_logo.json";
import * as anim2 from "../../assets/lottie/cycle_animation.json";
import * as anim3 from "../../assets/lottie/spinner_loading.json";

export default {
  components: {
    lottie: Lottie
  },
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      loginRules: {
        username: [{ required: true }],
        password: [{ required: true }]
      },
      animation: {
        options: [
          { animationData: anim1, loop: false },
          { animationData: anim2 },
          { animationData: anim3 }
        ],
        speed: 1
      },
      loading: false,
    };
  },
  methods: {
    handleAnimation: function(anim) {
      this.anim = anim;
      this.anim.setSpeed(this.animation.speed);
    },
    navigateToDashboard() {
      this.$router.push("/");
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          this.$store
            .dispatch("LoginByUsername", this.loginForm)
            .then(() => {
              this.loading = false;
              this.$router.push("/");
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              this.loading = false;
              this.$message.error(errorMsg[0]);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.el-container {
  height: 100vh;
}
.el-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(59,59,59, 1);
  border-right:1px solid rgba(59,59,59, 0.2);
  flex:5;
}
.el-aside {
  /* background-color: #d3dce6; */
  color: #333;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex:2;
}
.login-container {
  width: 70%;
  padding: 20px;
}
.signin-btn {
  width: 100%;
}
</style>



